import { useState } from "react";

const useInput = (validateFunction) => {
  const [elementEnteredValue, setElementEnteredValue] = useState("");
  const [elementIsTouched, setElementIsTouched] = useState(false);

  const elementValueIsValid = validateFunction(elementEnteredValue);
  const elementInputIsInvalid = !elementValueIsValid && elementIsTouched;

  const elementChangeHandler = (e) => {
    setElementEnteredValue(e.target.value);
  };

  const elementBLurHandler = () => {
    setElementIsTouched(true);
  };

  const reset = () => {
    setElementIsTouched(false);
    setElementEnteredValue("");
  };
  return {
    elementEnteredValue,
    elementValueIsValid,
    elementInputIsInvalid,
    elementChangeHandler,
    elementBLurHandler,
    reset,
  };
};

export default useInput;
