// import { useState } from "react";
import { useContext } from "react";
import useInput from "../../hooks/use-input";
import useRequest from "../../hooks/use-request";
import CartContext from "../../store/cart-context";

const Checkout = (props) => {
  const {
    elementEnteredValue: enteredName,
    elementValueIsValid: enteredNameIsValid,
    elementInputIsInvalid: nameInputIsInvalid,
    elementChangeHandler: nameInputChangeHandler,
    elementBLurHandler: nameInputBLurHandler,
    reset: resetNameInput,
  } = useInput((val) => val.trim() !== "");

  const {
    elementEnteredValue: enteredEmail,
    elementValueIsValid: enteredEmailIsValid,
    elementInputIsInvalid: emailInputIsInvalid,
    elementChangeHandler: emailInputChangeHandler,
    elementBLurHandler: emailInputBLurHandler,
    reset: resetEmailInput,
  } = useInput((val) => val.includes("@"));

  const {
    elementEnteredValue: enteredAddress,
    elementValueIsValid: enteredAddressIsValid,
    elementInputIsInvalid: addressInputIsInvalid,
    elementChangeHandler: addressInputChangeHandler,
    elementBLurHandler: addressInputBLurHandler,
    reset: resetAddressInput,
  } = useInput((val) => val.trim().length > 15);

  let formIsValid = false;

  if (enteredNameIsValid && enteredEmailIsValid && enteredAddressIsValid) {
    formIsValid = true;
  } else {
    formIsValid = false;
  }

  const { error, sendRequest: sendUserData } = useRequest();

  const cartCtx = useContext(CartContext);

  const submitHandler = (e) => {
    e.preventDefault();

    if (!formIsValid) {
      return;
    }
    console.log(enteredName);

    sendUserData(
      {
        url: "https://react-http-c8897-default-rtdb.asia-southeast1.firebasedatabase.app/Order.json",
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: {
          userName: enteredName,
          email: enteredEmail,
          address: enteredAddress,
          items: cartCtx.items.map((item) => {
            return { name: item["name"], Amount: item["amount"] };
          }),
        },
      },
      (data) => {
        console.log(data);
      }
    );

    props.submitSuccess();

    resetNameInput();
    resetEmailInput();
    resetAddressInput();
  };

  return (
    <form className="col-10 mx-auto mt-3 card p-2" onSubmit={submitHandler}>
      <h4 className="text-center">Checkout Details</h4>
      {error && <p className="text-danger">{error}</p>}
      <div className="form-floating">
        <input
          type="text"
          className="form-control"
          placeholder="Name"
          value={enteredName}
          onChange={nameInputChangeHandler}
          onBlur={nameInputBLurHandler}
        />
        <label>Name : </label>
        {nameInputIsInvalid && (
          <p className="text-danger mt-1 mb-0"> Name is Invalid </p>
        )}
      </div>
      <div className="form-floating">
        <input
          type="text"
          className="form-control"
          placeholder="example@email.com"
          value={enteredEmail}
          onChange={emailInputChangeHandler}
          onBlur={emailInputBLurHandler}
        />
        <label className="form-label">Email : </label>
        {emailInputIsInvalid && (
          <p className="text-danger mt-1 mb-0"> Email is Invalid </p>
        )}
      </div>
      <div className="form-floating">
        <textarea
          className="form-control"
          rows="10"
          placeholder="Address"
          value={enteredAddress}
          onChange={addressInputChangeHandler}
          onBlur={addressInputBLurHandler}
        ></textarea>
        <label className="form-label">Address : </label>
        {addressInputIsInvalid && (
          <p className="text-danger mt-1 mb-0"> Address is Invalid </p>
        )}
      </div>
      <div className="d-flex justify-content-end mt-2 py-2">
        <button
          className="btn btn-sm btn-outline-danger me-2"
          onClick={props.closeCheckout}
          type="button"
        >
          Cancel
        </button>
        <button
          className="btn btn-sm btn-outline-warning"
          onClick={props.onConfirm}
          disabled={!formIsValid}
        >
          Confirm
        </button>
      </div>
    </form>
  );
};

export default Checkout;
